import React, { Component } from 'react';
import PropTypes from 'prop-types';

class PlanetCard extends Component {
  render() {
    const { planetName, planetImage } = this.props;
    const altText = `Planeta ${planetName}`;
    const linkPlanet = `https://pt.wikipedia.org/wiki/${planetName}_(planeta)`;

    return (
      <div className='planet-card'>
        <label>{planetName}</label>
        <a href={linkPlanet} target="_blank" rel="noreferrer"><img src={ planetImage } alt={ altText } className="planet-image" /></a>

      </div>
    );
  }
}

PlanetCard.propTypes = {
  planetName: PropTypes.string.isRequired,
  planetImage: PropTypes.string.isRequired,
};

export default PlanetCard;
